.cpnt_editArea {
    padding: 20px;
    background-color: #1f1f1f;
}
.cpnt_editArea .container {
    position: relative;
    height: 100%;
    padding-bottom: 50px;
    white-space: pre-wrap;
    word-break: break-all;
    color: #fff;
    overflow-y: scroll;
}
.cpnt_editArea .container .input {
    position: absolute;
    width: 100%;
    height: 1000%;
    font-family: inherit;
    font-size: inherit;
    color: #fff;
    background-color: #1f1f1f;
    border: none;
    outline: none;
    resize: none;
    z-index: 1;
    overflow: visible;
}
.cpnt_editArea .container span {
    position: relative;
    white-space: pre-wrap;
    z-index: 0;
}
.cpnt_editArea .container .snippet {
    position: relative;
    color: #88d5ff;
    z-index: 2;
    opacity: 0.8;
    pointer-events: none;
}
