.cpnt_leftBar {
    padding: 20px;
    color: #fff;
    background-color: #181818;
    border-right: 1px solid #2a2a2a;
}
.cpnt_leftBar .mode {
    display: flex;
    margin-bottom: 20px;
}
.cpnt_leftBar .mode .label {
    margin-right: 20px;
    font-weight: bold;
}
.cpnt_leftBar .row {
    align-items: center;
    margin-bottom: 20px;
}
.cpnt_leftBar .row .info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
}
.cpnt_leftBar .row .info .label {
    font-weight: bold;
}
.cpnt_leftBar .row .info .value {
    font-size: 14px;
    color: #666;
}
.cpnt_leftBar .block {
    padding: 10px;
    color: #111;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 0 5px 0px rgba(0, 0, 0, 0.9) inset;
}
.cpnt_leftBar .block .row {
    margin-bottom: 10px;
}
